.menu {
  box-sizing: border-box;
  height: 100vh;
  background-color: #f9f9f9;
  font-weight: 700;
  overflow: auto;
  
  &__top {
    margin-bottom: 32px;

    @include onTablet {
      margin-bottom: 56px;
    }
  }
  
  &__nav {
    margin-bottom: 48px;
  }
  
  &__phone-number {
    display: block;
    margin-bottom: 16px;
  
    color: inherit;
    font-size: 16px;
    text-transform: uppercase;
    letter-spacing: 2px;
    line-height: 140%;
    text-decoration: none;
  }
  
  &__call-to-order {
    display: block;
    width: fit-content;
  
    padding-bottom: 3px;
    border-bottom: 1px solid $main-text-color;
  
    color: inherit;
    font-size: 16px;
    text-transform: uppercase;
    letter-spacing: 2px;
    line-height: 140%;
    text-decoration: none;
  }
}
